export const CartReducer=(state,action)=>{
    const{shoppingCart,totalPrice,qty}=state
    let product;
    let index;
    let updatePrice;
    let updatedQty



    switch(action.type){
        case 'Add_to_cart':
            const check=shoppingCart.find(product=>product.id===action.id)
            if(check){
                return state;
            }else{
                product=action.product
                product['qty']=1;
                updatedQty=qty+1
                updatePrice=totalPrice+product.price
                return {
                    shoppingCart:[product,...shoppingCart],
                    totalPrice:updatePrice,qty:updatedQty
                }
            }

       

            case 'INC':
                product=action.cart;
                product.qty=product.qty+1;
                updatePrice=totalPrice+product.price;
                updatedQty=qty+1;
                index=shoppingCart.findIndex(cart=>cart.id===action.id);
                shoppingCart[index]=product;
                return {shoppingCart:[...shoppingCart],totalPrice:updatePrice,qty:updatedQty}
              

               case 'DEC':
                   product=action.cart
                   if(product.qty>1){
                       product.qty=product.qty-1;
                       updatePrice=totalPrice-product.price;
                       updatedQty=qty-1;
                       index=shoppingCart.findIndex(cart=>cart.id===action.id);
                       shoppingCart[index]=product;
                       return{shoppingCart:[...shoppingCart],totalPrice:updatePrice,qty:updatedQty}

                   }else{
                       return state
                   }
                

                   case 'DELETE':
                       const filtered=shoppingCart.filter(product=>product.id !==action.id)
                       product=action.cart;
                       updatedQty=qty-product.qty;
                       updatePrice=totalPrice-product.price*product.qty;
                       return{shoppingCart:[...filtered],totalPrice:updatePrice,qty:updatedQty}

                
            
                default:
                return state;
          
           
    }
}