import React,{useContext} from 'react'
import { ProductsContext } from '../Global/ProductsContext'
import { CartContext } from '../Global/CartContext'
import Banner from './Banner'
import { Card,Button } from 'react-bootstrap'


const Products = () => {
    const {products}=useContext(ProductsContext)
    const {dispatch}=useContext(CartContext)
    
    
    
    return (
        <div className="container"> 
        <Banner />
        <div className=" row">
            {products.map((product)=>(
                // <div className="product" key={product.id}>
                   
                //     <div className="product-image">
                //         <img src={product.image} alt="not found" />
                //     </div>
                //     <div className="product-details">
                //         <div className="product-name">
                //             {product.name}
                //         </div>
                //         <div className="product-price">
                //             ${product.price}.00
                //         </div>
                //     </div>
                    
                  
                    
                //     <div className="add-to-cart" onClick={()=>dispatch({type:'Add_to_cart',id:product.id,product})}>add to cart</div>
                //     {/* {product.status==='hot'?<div className="hot">Hot</div>:''}
                //     {product.status==='new'?<div className="new">New</div>:''} */}

                // </div>
                   <div className="col-lg-3  col-md-4 col-sm-6 col-xs-12 text-xs-center mt-4 d-flex d-block ">


                 <Card className="product ">
                    <Card.Img  variant="top" src={product.image} />
                    <Card.Body className="text-center">
                        <Card.Title className="robin">{product.name}</Card.Title>
                        <Card.Text>
                       ${product.price}.00
                        </Card.Text>
                        <Button onClick={()=>dispatch({type:'Add_to_cart',id:product.id,product})} variant="success">ADD TO CART</Button>
                        {/* {product.status==='hot'?<div className="hot">Hot</div>:''}
                         {product.status==='new'?<div className="new">New</div>:''}  */}
                    </Card.Body>
                    </Card>
                   </div>
            ))}

            
            
        </div>

   

          
        </div>
    )
}

export default Products
